section {
  display: flex;
  width: 600;
  height: 600px;
}

section img {
  width: 0;
  flex-grow: 1;
  object-fit: cover;
  opacity: 0.8;
  transition: 0.5s ease;
}

section img:hover {
  cursor: crosshair;
  width: 300px;
  height: 700;
  opacity: 1;
  filter: contrast(120%);
}
